.App {
  text-align: center;
}

.App-header {
  background-color: #00a0b0;
  height: 100px;
  padding: 20px;
  text-align: center;
  color: white;
  border-bottom: 3px solid;
  border-bottom-color: rgba(255, 255, 255, .7);
}

.App-title {
  font-size: 35px;
}

.App-intro {
  font-size: large;
}

.Player-Turn {
  color: #f92f31;
}

.Player2-Turn {
  color: #ffb900;
}

#cell {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #3786f9;
  height: 100px;
  width: 100px;
  float: left;
  text-align: center;
}

.board {
  margin: 0 auto;
  margin-top: 20px;
  width: 700px;
}

#circle {
  background-color: white;
  height: 70px;
  width: 70px;
  margin: 0 auto;
  border-radius: 50%;
}

#chipRed {
  background-color: #f92f31;
  height: 70px;
  width: 70px;
  margin: 0 auto;
  border-radius: 50%;
}

#chipYellow {
  background-color: #ffb900;
  height: 70px;
  width: 70px;
  margin: 0 auto;
  border-radius: 50%;
}

.slideInDown5 {
  -webkit-animation-name: slideInDown5;
          animation-name: slideInDown5;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes slideInDown5 {
  from {
    -webkit-transform: translate3d(0, -520px, 0);
            transform: translate3d(0, -520px, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInDown5 {
